.content {
    overflow-x: hidden;
    width: 100vw;
    background-color: #0F1010;
    position: relative;
    scroll-behavior: smooth;
}

.welcomeSection {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background-color: #30BA09;
    background-position: center;
    background-size: cover;
    opacity: 1;
}


.welcomeSection > h2 {
    text-align: center;
    color: #fff;
    font-family: 'Stolzl', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 8rem;
    line-height: 8rem;
    z-index: 2;
}

.welcomeSection > h2 > span {
    font-family: 'Apple Garamond CYR';
    text-transform: capitalize;
    font-size: 1em;
    line-height: 1;
}

.welcomeLogo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 16.6rem;
    height: 22rem;
    mask-image: url(../../assets/union-hookah/logo-main.svg);
    -webkit-mask-image: url(../../assets/union-hookah/logo-main.svg);
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
    background-color: #000;
}

.titleBtn {
    position: absolute;
    top: 10rem;
    font-size: 2rem;
    line-height: 2.4rem;
    font-family: 'Stolzl', sans-serif;
    color: #fff;
    cursor: pointer;
}

.section1 {
    margin-top: 20rem;
    position: relative;
    height: calc(120rem + 800px);
}

.unionFirstBg {
    position: absolute;
    top: 23rem;
    left: 10rem;
    width: 95.5rem;
    height: 88rem;
    background-image: url(../../assets/union-hookah/bg.png);
    background-size: cover;
}

.unionFirstFont {
    position: absolute;
    top: 23rem;
    left: 10rem;
    width: 95.5rem;
    height: 88rem;
    background-image: url(../../assets/union-hookah/logo.png);
    background-size: cover;
}

.unionFirstTopFont {
    z-index: 5;
    position: absolute;
    top: 23rem;
    left: 10rem;
    width: 95.5rem;
    height: 88rem;
    background-image: url(../../assets/union-hookah/logo2.png);
    background-size: cover;
}

.leftDudka {
    position: absolute;
    top: calc(23rem + 800px);
    left: 10rem;
    width: 95.5rem;
    height: 112.7rem;
    background-image: url(../../assets/union-hookah/truba_L.png);
    background-size: cover;
}

.rightDudka {
    position: absolute;
    top: calc(23rem + 800px);
    left: 10rem;
    width: 95.5rem;
    height: 112.7rem;
    background-image: url(../../assets/union-hookah/truba_R.png);
    background-size: cover;
}

.section2 {
    margin: 20rem 0;
    position: relative;
    height: 100vh;
}

.logoU {
    width: 95rem;
    height: 36rem;
    position: absolute;
    top: 50vh;
    left: 50vw;
    transform: translateX(-50%);
    background-image: url(../../assets/union-hookah/logo-animation/logoU.png);
    background-size: cover;
}

.logoNion {
    width: 95rem;
    height: 36rem;
    position: absolute;
    top: 50vh;
    left: 50vw;
    transform: translateX(-50%);
    background-image: url(../../assets/union-hookah/logo-animation/logoNion.png);
    background-size: cover;
}

.logoHookah {
    width: 95rem;
    height: 36rem;
    position: absolute;
    top: 50vh;
    left: 50vw;
    transform: translateX(-50%);
    background-image: url(../../assets/union-hookah/logo-animation/logoHookah.png);
    background-size: cover;
}

.section3 {
    margin-top: 30rem;
    position: relative;
    height: 100vh;
}

.bags {
    width: 100vw;
    height: 112.5rem;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../../assets/union-hookah/bags/bags.png);
    background-size: cover;
    background-position: center;
    z-index: 10;
}

.topMotionText {
    width: 232rem;
    height: 112.5rem;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-image: url(../../assets/union-hookah/bags/top_bottom.png);
    background-size: cover;
    background-position: center;
}

.section4 {
    margin: 30rem 0;
    position: relative;
    height: 200vh;
}

.centerMotionText {
    width: 232rem;
    height: 112.5rem;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-image: url(../../assets/union-hookah/bags/center.png);
    background-size: cover;
    background-position: center;
}

.blackSquareContainer {
    margin: 20rem 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.blackSquare {
    background-color: #000;
    width: 50vw;
    height: 50vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sitePage {
    z-index: 15;
    position: absolute;
    top: 50vh;
    left: 50vw;
    width: 0;
    height: 0;
    background-image: url(../../assets/union-hookah/site-animation/site-page.png);
    background-size: cover;
    background-position: center;
}

.section5 {
    margin: 30rem 0;
    position: relative;
    height: calc(300vh + 55rem);
}

.whiteSquareContainer {
    z-index: 2;
    margin: 40rem 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.whiteSquare {
    background-color: #fff;
    width: 50vw;
    height: 50vw;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.logo {
    width: 51rem;
    height: 3.7rem;
    mask-image: url(../../assets/union-hookah/site-animation/logo.svg);
    -webkit-mask-image: url(../../assets/union-hookah/site-animation/logo.svg);
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
}

.mockUpContainer {
    z-index: 15;
    position: absolute;
    top: 50vh;
    left: 50vw;
    width: 0;
    height: 0;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mockUpBook {
    height: 47rem;
    width: 94rem;
    background-image: url(../../assets/union-hookah/site-animation/book.jpg);
    background-size: cover;
    background-position: center;
}

.nextProject {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    height: 100vh;
    width: 100vw;
    z-index: 1;
    justify-content: center;
    align-items: center;
}

.nextProject > div {
    text-transform: uppercase;
    font-family: 'Stolzl', sans-serif;
    font-size: 8rem;
    line-height: 9.6rem;
    color: #fff;
    cursor: pointer;
}

@media(max-width: 767px) {

    .titleBtn {
        top: 10vh;
        transform: translateY(-50%);
    }
    
    .welcomeSection > h2 {
        font-size: 2.5rem;
        line-height: 2.5rem;
    }

    .welcomeLogo {
        width: 6.4rem;
        height: 8rem;
    }
    
    .unionFirstBg {
        left: 2.6rem;
        width: 37.6rem;
        height: 34.6rem;
    }
    
    .unionFirstFont {
        left: 2.6rem;
        width: 37.6rem;
        height: 34.6rem;
    }
    
    .unionFirstTopFont {
        left: 2.6rem;
        width: 37.6rem;
        height: 34.6rem;
    }
    
    .leftDudka {
        left: 2.6rem;
        width: 37.6rem;
        height: 44.5rem;
    }
    
    .rightDudka {
        left: 2.6rem;
        width: 37.6rem;
        height: 44.5rem;
    }

    .logoU {
        width: 37.6rem;
        height: 14.1rem;
    }
    
    .logoNion {
        width: 37.6rem;
        height: 14.1rem;
    }

    .logoHookah {
        width: 37.6rem;
        height: 14.1rem;
    }
    
    .bags {
        width: 100vw;
        height: 31rem;
        position: absolute;
        left: 0;
        background-image: url(../../assets/union-hookah/bags/bags.png);
        background-size: cover;
        background-position: center;
        z-index: 10;
    }
    
    .topMotionText {
        width: 64rem;
        height: 31rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../../assets/union-hookah/bags/top_bottom.png);
        background-size: cover;
        background-position: center;
    }
    
    .centerMotionText {
        width: 64rem;
        height: 31rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../../assets/union-hookah/bags/center.png);
        background-size: cover;
        background-position: center;
    }
    
    .blackSquareContainer {
        margin: 20rem 0;
        position: absolute;
        left: 0;
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .blackSquare {
        background-color: #000;
        width: 80vw;
        height: 80vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .sitePage {
        background-size: contain;
        background-repeat: no-repeat;
    }
    
    .whiteSquareContainer {
        margin: 40rem 0;
        position: absolute;
    }
    
    .whiteSquare {
        width: 80vw;
        height: 80vw;
    }
    
    .logo {
        width: 27rem;
        height: 1.95rem;
        mask-image: url(../../assets/union-hookah/site-animation/logo.svg);
        -webkit-mask-image: url(../../assets/union-hookah/site-animation/logo.svg);
        mask-position: center;
        mask-size: contain;
        mask-repeat: no-repeat;
        -webkit-mask-repeat: no-repeat;
    }
    
    
    .mockUpBook {
        height: 47rem;
        width: 94rem;
        background-size: contain;
        background-repeat: no-repeat;
    }

    .section1 {
        height: calc(90rem + 800px);
    }

    .section2 {
        margin: 0;
    }

    .section3 {
        height: 30vh;
    }

    .section4 {
        margin: 0;
    }

  }